.feedback_list {
    display: flex;
}
.item + .item {
    margin-left: 10px;
}

.button {
    align-items: center;
    appearance: none;
    background-color: #fff;
    border-radius: 24px;
    border-style: none;
    box-shadow: #00000033 0 3px 5px -1px, #00000024 0 6px 10px 0,
        #0000001f 0 1px 18px 0;
    box-sizing: border-box;
    color: #3c4043;
    cursor: pointer;
    display: inline-flex;
    font-size: 14px;
    font-weight: 500;
    height: 48px;
    justify-content: center;
    letter-spacing: 0.25px;
    line-height: normal;
    max-width: 100%;
    overflow: visible;
    padding: 2px 24px;
    position: relative;
    text-align: center;
    text-transform: none;
    transition: box-shadow 280ms var(--timing-function),
        opacity 15ms linear 30ms, transform 270ms var(--timing-function) 0ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: auto;
    will-change: transform, opacity;
    z-index: 0;
}

.button:hover,
.button:focus {
    background: #f6f9fe;
    color: var(--indigo-accent);
    outline: none;
    border: 2px solid var(--indigo);
}
